html {
  margin: 0 auto;
  background: linear-gradient(rgba(174, 249, 254, 0.5), rgba(0, 0, 0, 0.5)), url("UZ3ZE3T.png");
  background-repeat: no-repeat;
  background-size: cover;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

.App {
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.newList {
  width: 600px;
  height: 100px;
}

.newList h3 {
  margin-bottom: 10px;
  color: rgba(79, 68, 68, 0.99)
}

.lists {
  display: flex;
  margin-left: 50px;
}
